import React from 'react';
import { Text, TextFontColor } from 'core/src/typography';
import { InputHintProps } from 'shared/types/input';

const InputHint: React.FC<InputHintProps> = ({
  disabled,
  isError,
  dataTestId,
  hintMessage
}): JSX.Element => {
  let textColor: TextFontColor = 'secondary';

  if (isError) { textColor = 'negative_dark'; }

  if (disabled) { textColor = 'disabled'; }

  return (
    <Text
      dataTestId={ dataTestId }
      size="extra_small"
      color={ textColor }
      inline
    >
      { hintMessage }
    </Text>
  );
};

const MemoizedInputHint = React.memo(InputHint);

export { MemoizedInputHint };
